'use client';

import React, { useEffect, useRef, useState } from 'react';
import { MapPin, Phone, User, CheckCircle, Loader2, ArrowLeft, ArrowRight, Truck, Crosshair, Mail } from 'lucide-react';
import { useCart } from '@/context/CartContext';
import { api } from '@/services/api';
import { useRouter } from 'next/navigation';
import Link from 'next/link';
import { PaymentMethodSelector } from '@/components/ui/payment-method-selector';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Input } from '@/components/ui/input';
import { Textarea } from '@/components/ui/textarea';
import { Label } from '@/components/ui/label';

// Declare Leaflet global type
declare const L: any;

export default function CheckoutPage() {
    const { cart, clearCart } = useCart();
    const router = useRouter();
    const mapRef = useRef<any>(null);
    const mapContainerRef = useRef<HTMLDivElement>(null);
    const markerRef = useRef<any>(null);

    const [formData, setFormData] = useState({ name: '', phone: '', email: '', addressNote: '', address: '' });
    const [paymentMethod, setPaymentMethod] = useState<'cash' | 'online'>('cash');
    const [paymentError, setPaymentError] = useState<string | null>(null);
    const [location, setLocation] = useState<{ lat: number, lng: number } | null>(null);
    const [orderPlaced, setOrderPlaced] = useState(false);
    const [isSubmitting, setIsSubmitting] = useState(false);

    // Real-time Fee Estimation State
    const [estimatedFee, setEstimatedFee] = useState<number>(1000);
    const [isEstimating, setIsEstimating] = useState(false);
    const [distanceKm, setDistanceKm] = useState<number>(0);

    // Autocomplete State
    const [suggestions, setSuggestions] = useState<any[]>([]);
    const [showSuggestions, setShowSuggestions] = useState(false);
    const searchTimeoutRef = useRef<any>(null);

    // Totals Calculation
    const subTotal = cart.reduce((sum, item) => sum + (item.price * item.quantity), 0);
    const totalItems = cart.reduce((acc, item) => acc + item.quantity, 0);
    const isEligibleForOffer = totalItems >= 2;

    const deliveryFee = isEligibleForOffer ? 0 : estimatedFee;
    const discountAmount = isEligibleForOffer ? subTotal * 0.05 : 0;
    const total = subTotal + deliveryFee - discountAmount;

    // Redirect if cart empty
    useEffect(() => {
        if (cart.length === 0 && !orderPlaced) {
            router.push('/produits');
        }
    }, [cart, router, orderPlaced]);

    // Core Update Logic
    const updateLocation = async (lat: number, lng: number, fromMapDrag: boolean = false) => {
        setLocation({ lat, lng });

        setIsEstimating(true);
        try {
            const result = await api.estimateDelivery(`${lat},${lng}`, subTotal);
            if (result) {
                setEstimatedFee(result.fee);
                setDistanceKm(result.distanceKm);

                if (fromMapDrag && result.destination?.address) {
                    setFormData(prev => ({ ...prev, address: result.destination.address }));
                }
            }
        } catch (e) {
            console.error(e);
        } finally {
            setIsEstimating(false);
        }
    };

    // Initialize Map
    useEffect(() => {
        // Wait for Leaflet to be loaded
        if (typeof L === 'undefined') {
            const interval = setInterval(() => {
                if (typeof L !== 'undefined') {
                    clearInterval(interval);
                    initMap();
                }
            }, 100);
            return () => clearInterval(interval);
        } else {
            initMap();
        }

        function initMap() {
            try {
                if (mapContainerRef.current && !mapRef.current) {
                    const defaultLat = 5.3484;
                    const defaultLng = -4.0305;

                    const map = L.map(mapContainerRef.current, {
                        maxBounds: [[4.3, -8.7], [10.8, -2.5]],
                        minZoom: 6
                    }).setView([defaultLat, defaultLng], 13);

                    // PROXY LOCAL (Nginx)
                    L.tileLayer('http://localhost:8081/osm/{z}/{x}/{y}.png', {
                        attribution: '&copy; OpenStreetMap contributors'
                    }).addTo(map);

                    const icon = L.icon({
                        iconUrl: 'https://unpkg.com/leaflet@1.7.1/dist/images/marker-icon.png',
                        shadowUrl: 'https://unpkg.com/leaflet@1.7.1/dist/images/marker-shadow.png',
                        iconSize: [25, 41],
                        iconAnchor: [12, 41]
                    });

                    const marker = L.marker([defaultLat, defaultLng], { draggable: true, icon }).addTo(map);
                    markerRef.current = marker;
                    setLocation({ lat: defaultLat, lng: defaultLng });

                    marker.on('dragend', function (event: any) {
                        const { lat, lng } = marker.getLatLng();
                        updateLocation(lat, lng, true);
                    });

                    map.on('click', function (e: any) {
                        marker.setLatLng(e.latlng);
                        const { lat, lng } = e.latlng;
                        updateLocation(lat, lng, true);
                    });

                    mapRef.current = map;

                    if (!location) {
                        updateLocation(defaultLat, defaultLng);
                    }
                }
            } catch (error) {
                console.error("Map initialization failed:", error);
            }
        }
    }, []);

    const handleAddressChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        const val = e.target.value;
        setFormData({ ...formData, address: val });

        if (searchTimeoutRef.current) clearTimeout(searchTimeoutRef.current);

        if (val.length > 2) {
            searchTimeoutRef.current = setTimeout(async () => {
                const results = await api.searchPlaces(val);
                setSuggestions(results);
                setShowSuggestions(true);
            }, 300);
        } else {
            setSuggestions([]);
            setShowSuggestions(false);
        }
    };

    const handleSuggestionClick = (place: any) => {
        setFormData({ ...formData, address: place.display_name });

        if (mapRef.current && markerRef.current) {
            const newLatLng = new L.LatLng(place.lat, place.lon);
            markerRef.current.setLatLng(newLatLng);
            mapRef.current.panTo(newLatLng);
        }

        setLocation({ lat: place.lat, lng: place.lon });

        api.estimateDelivery(`${place.lat},${place.lon}`, subTotal).then(res => {
            if (res) {
                setEstimatedFee(res.fee);
                setDistanceKm(res.distanceKm);
            }
        });

        setSuggestions([]);
        setShowSuggestions(false);
    };

    const handleGeolocation = () => {
        if (!navigator.geolocation) {
            alert("La géolocalisation n'est pas supportée par votre navigateur.");
            return;
        }

        setIsEstimating(true);
        navigator.geolocation.getCurrentPosition(
            (position) => {
                const { latitude, longitude } = position.coords;
                if (latitude < 4.3 || latitude > 10.8 || longitude < -8.7 || longitude > -2.5) {
                    alert("Votre position semble être hors de la Côte d'Ivoire.");
                    setIsEstimating(false);
                    return;
                }

                if (mapRef.current && markerRef.current) {
                    const newLatLng = new L.LatLng(latitude, longitude);
                    markerRef.current.setLatLng(newLatLng);
                    mapRef.current.setView(newLatLng, 15);
                    updateLocation(latitude, longitude, true);
                }
            },
            (error) => {
                console.error(error);
                setIsEstimating(false);
                alert("Impossible de récupérer votre position.");
            }
        );
    };

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();
        setPaymentError(null);

        if (!formData.name || !formData.phone || !location) {
            alert("Veuillez remplir vos informations.");
            return;
        }

        setIsSubmitting(true);

        // CASH ON DELIVERY FLOW
        if (paymentMethod === 'cash') {
            const orderId = 'CMD-' + Math.floor(Math.random() * 1000000);
            try {
                await api.createDelivery({
                    orderId,
                    address: `${formData.address} (GPS: ${location.lat.toFixed(5)}, ${location.lng.toFixed(5)}) ${formData.addressNote ? '- ' + formData.addressNote : ''}`,
                    lat: location.lat,
                    lon: location.lng,
                    fee: deliveryFee,
                    customerName: formData.name,
                    customerPhone: formData.phone
                });
                setOrderPlaced(true);
                clearCart();
            } catch (error) {
                console.error(error);
                setPaymentError("Erreur lors de la commande. Veuillez réessayer.");
            } finally {
                setIsSubmitting(false);
            }
            return;
        }

        // ONLINE PAYMENT FLOW (Moneroo)
        try {
            const result = await api.initiatePayment({
                cart: cart,
                customer: {
                    name: formData.name,
                    phone: formData.phone,
                    email: formData.email || undefined
                },
                delivery: {
                    address: `${formData.address} (GPS: ${location.lat.toFixed(5)}, ${location.lng.toFixed(5)})`,
                    lat: location.lat,
                    lon: location.lng,
                    fee: deliveryFee,
                    addressNote: formData.addressNote || undefined
                }
            });

            sessionStorage.setItem('pendingOrderId', result.orderId);
            window.location.href = result.checkoutUrl;

        } catch (error: any) {
            console.error('Payment error:', error);
            setPaymentError(error.response?.data?.message || "Erreur lors de l'initialisation du paiement. Veuillez réessayer.");
            setIsSubmitting(false);
        }
    };

    const handleWhatsApp = () => {
        const url = `https://wa.me/2250143678397?text=Mon%20numero%20commande%20${Math.floor(Math.random() * 1000)}`;
        window.open(url, '_blank');
    };

    if (orderPlaced) {
        return (
            <div className="min-h-screen bg-gray-50 flex flex-col items-center justify-center p-4">
                <div className="bg-white p-8 rounded-2xl shadow-xl text-center max-w-md w-full">
                    <div className="w-20 h-20 bg-green-100 rounded-full flex items-center justify-center mx-auto mb-6">
                        <CheckCircle className="w-10 h-10 text-green-600" />
                    </div>
                    <h2 className="text-2xl font-bold text-gray-900 mb-2">Commande Reçue !</h2>
                    <p className="text-gray-600 mb-8">
                        Merci {formData.name}. Nous vous contacterons au {formData.phone} pour la livraison.
                    </p>
                    <div className="space-y-3">
                        <Link href="/" className="block w-full py-3 border border-gray-300 rounded-xl font-bold text-gray-700 hover:bg-gray-50 flex items-center justify-center">
                            Retour à l'accueil
                        </Link>
                        <button onClick={handleWhatsApp} className="w-full py-3 bg-[#25D366] text-white rounded-xl font-bold hover:bg-[#128C7E]">
                            Suivre sur WhatsApp
                        </button>
                    </div>
                </div>
            </div>
        );
    }

    if (cart.length === 0) return null; // Or loader

    return (
        <div className="min-h-screen bg-gray-50 flex flex-col text-gray-900">
            {/* Header */}
            <div className="bg-white border-b border-gray-200 sticky top-0 z-30 px-4 py-4 md:px-8 flex items-center gap-4">
                <Link href="/produits" className="p-2 hover:bg-gray-100 rounded-full">
                    <ArrowLeft className="w-6 h-6" />
                </Link>
                <h1 className="text-xl font-bold">Finaliser la commande</h1>
            </div>

            <div className="flex-1 max-w-7xl mx-auto w-full grid grid-cols-1 lg:grid-cols-2 gap-8 p-4 md:p-8">

                {/* Left Col: Map */}
                <div className="space-y-4">
                    <div className="bg-white p-6 rounded-2xl shadow-sm border border-gray-100 h-full flex flex-col">
                        <h2 className="font-bold text-lg mb-4 flex items-center">
                            <MapPin className="w-5 h-5 mr-2 text-primary-600" />
                            Lieu de livraison
                        </h2>

                        {/* Address Input */}
                        <div className="mb-4 relative">
                            <input
                                type="text"
                                className="pl-10 w-full p-3 border border-gray-300 rounded-xl focus:ring-2 focus:ring-primary-500 focus:border-transparent outline-none transition-all shadow-sm"
                                placeholder="Rechercher une adresse (ex: Riviera 3)..."
                                value={formData.address}
                                onChange={handleAddressChange}
                                onBlur={() => setTimeout(() => setShowSuggestions(false), 200)}
                            />
                            <MapPin className="absolute left-3 top-3.5 w-5 h-5 text-gray-400" />
                            {isEstimating && <div className="absolute right-3 top-3.5"><Loader2 className="w-5 h-5 animate-spin text-primary-600" /></div>}

                            {showSuggestions && suggestions.length > 0 && (
                                <div className="absolute top-full left-0 right-0 z-50 mt-1 bg-white border border-gray-200 rounded-xl shadow-xl max-h-60 overflow-y-auto">
                                    {suggestions.map((place, idx) => (
                                        <div
                                            key={idx}
                                            className="p-3 hover:bg-gray-50 cursor-pointer border-b border-gray-50 last:border-0 text-sm"
                                            onClick={() => handleSuggestionClick(place)}
                                        >
                                            <p className="font-medium text-gray-800 truncate">{place.display_name?.split(',')[0]}</p>
                                            <p className="text-xs text-gray-500 truncate">{place.display_name}</p>
                                        </div>
                                    ))}
                                </div>
                            )}
                        </div>

                        <div className="flex-1 min-h-[400px] rounded-xl overflow-hidden border border-gray-300 relative z-0">
                            <div ref={mapContainerRef} className="w-full h-full" />
                            <button
                                type="button"
                                onClick={handleGeolocation}
                                className="absolute bottom-4 right-4 z-[400] bg-white p-3 rounded-full shadow-lg hover:bg-gray-50 transition-all border border-gray-200"
                                title="Ma position"
                            >
                                <Crosshair className="w-6 h-6 text-gray-700" />
                            </button>
                        </div>

                        <div className="mt-4 flex items-center justify-between p-4 bg-gray-50 rounded-xl">
                            <div>
                                <p className="text-sm text-gray-500">Distance</p>
                                <p className="font-bold text-lg">{distanceKm} km</p>
                            </div>
                            <div className="text-right">
                                <p className="text-sm text-gray-500">Frais de livraison</p>
                                <p className="font-bold text-xl text-primary-600">
                                    {isEligibleForOffer ? <span className="text-green-600">Offerts</span> : `${estimatedFee} FCFA`}
                                </p>
                            </div>
                        </div>
                    </div>
                </div>

                {/* Right Col: Form & Summary */}
                <div className="space-y-6">
                    {/* Summary */}
                    <div className="bg-white p-6 rounded-2xl shadow-sm border border-gray-100">
                        <h3 className="font-bold text-gray-800 mb-4">Récapitulatif</h3>
                        <div className="space-y-3">
                            {cart.map(item => (
                                <div key={item.id} className="flex justify-between items-center text-sm">
                                    <div className="flex items-center gap-3">
                                        <span className="font-bold text-gray-500">{item.quantity}x</span>
                                        <span>{item.name}</span>
                                    </div>
                                    <span>{(item.price * item.quantity).toLocaleString()} F</span>
                                </div>
                            ))}
                            <div className="border-t border-gray-100 my-2 pt-2 space-y-2">
                                <div className="flex justify-between text-gray-600">
                                    <span>Sous-total</span>
                                    <span>{subTotal.toLocaleString()} F</span>
                                </div>
                                <div className="flex justify-between text-gray-600">
                                    <span>Livraison</span>
                                    <span>{deliveryFee === 0 ? 'Gratuite' : `${deliveryFee} F`}</span>
                                </div>
                                <div className="flex justify-between text-xl font-bold text-gray-900 pt-2">
                                    <span>Total à payer</span>
                                    <span>{total.toLocaleString()} F</span>
                                </div>
                            </div>
                        </div>
                    </div>

                    {/* Form */}
                    <div className="bg-white p-6 rounded-2xl shadow-sm border border-gray-100">
                        <h3 className="font-bold text-gray-800 mb-4">Vos Coordonnées</h3>
                        <form onSubmit={handleSubmit} className="space-y-4">
                            <div>
                                <label className="block text-sm font-medium text-gray-700 mb-1">Nom complet</label>
                                <div className="relative">
                                    <User className="absolute left-3 top-3 w-5 h-5 text-gray-400" />
                                    <input
                                        type="text"
                                        className="pl-10 w-full p-3 border border-gray-300 rounded-xl focus:ring-2 focus:ring-primary-500 focus:border-transparent outline-none transition-all"
                                        placeholder="Votre nom"
                                        required
                                        value={formData.name}
                                        onChange={e => setFormData({ ...formData, name: e.target.value })}
                                    />
                                </div>
                            </div>
                            <div>
                                <label className="block text-sm font-medium text-gray-700 mb-1">Téléphone</label>
                                <div className="relative">
                                    <Phone className="absolute left-3 top-3 w-5 h-5 text-gray-400" />
                                    <input
                                        type="tel"
                                        className="pl-10 w-full p-3 border border-gray-300 rounded-xl focus:ring-2 focus:ring-primary-500 focus:border-transparent outline-none transition-all"
                                        placeholder="+225..."
                                        required
                                        value={formData.phone}
                                        onChange={e => setFormData({ ...formData, phone: e.target.value })}
                                    />
                                </div>
                            </div>
                            <div>
                                <label className="block text-sm font-medium text-gray-700 mb-1">Email (facultatif)</label>
                                <div className="relative">
                                    <Mail className="absolute left-3 top-3 w-5 h-5 text-gray-400" />
                                    <input
                                        type="email"
                                        className="pl-10 w-full p-3 border border-gray-300 rounded-xl focus:ring-2 focus:ring-primary-500 focus:border-transparent outline-none transition-all"
                                        placeholder="votre@email.com"
                                        value={formData.email}
                                        onChange={e => setFormData({ ...formData, email: e.target.value })}
                                    />
                                </div>
                            </div>
                            <div>
                                <label className="block text-sm font-medium text-gray-700 mb-1">Note pour le livreur</label>
                                <textarea
                                    className="w-full p-3 border border-gray-300 rounded-xl focus:ring-2 focus:ring-primary-500 focus:border-transparent outline-none transition-all"
                                    placeholder="Précisions..."
                                    rows={2}
                                    value={formData.addressNote}
                                    onChange={e => setFormData({ ...formData, addressNote: e.target.value })}
                                />
                            </div>

                            {/* Payment Method Selection */}
                            <div>
                                <Label className="block text-sm font-medium text-gray-700 mb-3">Mode de paiement</Label>
                                <PaymentMethodSelector
                                    value={paymentMethod}
                                    onChange={setPaymentMethod}
                                />
                            </div>

                            {paymentError && (
                                <div className="p-3 bg-red-50 border border-red-200 rounded-xl text-red-700 text-sm">
                                    {paymentError}
                                </div>
                            )}

                            <button
                                type="submit"
                                disabled={isSubmitting}
                                className="w-full bg-gray-900 text-white py-4 rounded-xl font-bold text-lg hover:bg-gray-800 transition-all shadow-lg text-center flex justify-center items-center gap-2"
                            >
                                {isSubmitting ? (
                                    <Loader2 className="animate-spin" />
                                ) : paymentMethod === 'cash' ? (
                                    'Confirmer la commande'
                                ) : (
                                    'Payer maintenant'
                                )}
                                <ArrowRight className="w-5 h-5" />
                            </button>
                            <p className="text-center text-xs text-gray-500">
                                {paymentMethod === 'cash'
                                    ? 'Paiement en espèces à la livraison'
                                    : 'Paiement sécurisé par Mobile Money'}
                            </p>
                        </form>
                    </div>
                </div>
            </div>
        </div>
    );
}
